import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import FactoryIcon from '@mui/icons-material/Factory';
import PersonIcon from '@mui/icons-material/Person';

import {
  Loader,
  SearchField,
  SearchMultiField,
  // SearchLocationField,
  // DateTimeField,
  SelectMultiField,
  TextField,
  SelectField,
} from '../components';

import {
  CustomersQuery,
  LoadCreate,
  // LoadsQuery,
  TrucksQuery,
  UsersQuery,
} from '../api';
import { useSnackbar } from '../hooks';
import {
  CreateLoad,
  User,
  Truck,
  TruckTypes,
  LoadStatuses,
  LoadStatus,
  Customer,
} from '../utils/dataTypes';
import Divider from '@mui/material/Divider';

const validationSchema = yup.object({
  ref: yup.array().of(yup.string().required('Ref must not be empty')).max(3),
  status: yup.string().oneOf(LoadStatuses).required('Status is required'),
  /*pick: yup
    .object({
      formatted_address: yup.string().required(),
      geometry: yup
        .object({
          location: yup
            .object({
              lat: yup.mixed().required(),
              lng: yup.mixed().required(),
            })
            .required(),
        })
        .required(),
    })
    .required('Pick location is required'),
  pickDate: yup
    .date()
    .typeError('Enter correct date')
    .required('Pick Date is required'),
  deliver: yup
    .object({
      formatted_address: yup.string().required(),
      geometry: yup
        .object({
          location: yup
            .object({
              lat: yup.mixed().required(),
              lng: yup.mixed().required(),
            })
            .required(),
        })
        .required(),
    })
    .required('Deliver location is required'),
  deliverDate: yup
    .date()
    .typeError('Enter correct date')
    .required('Deliver Date is required'),*/
  weight: yup.string().required('Weight is required'),
  truckType: yup
    .array(yup.string().oneOf(TruckTypes))
    .min(1, 'Select at list one')
    .required('Truck Type is required'),
  rate: yup.number().typeError('Rate must be a number'),
  totalCharges: yup.number().typeError('Total charges must be a number'),
  currency: yup.string().required('Currency is required'),
  bookedByUser: yup
    .object({ id: yup.string().required() })
    .typeError('Enter correct user')
    .nullable(),
  bookedByCompany: yup.string(),
  assignTo: yup.array(
    yup.object({ id: yup.string().required() }).typeError('Enter correct user'),
  ),
  checkInAs: yup.string(),
  truck: yup
    .object({ id: yup.string().required() })
    .typeError('Enter correct truck')
    .nullable(),
  bookedWith: yup
    .object({ id: yup.string().required() })
    .typeError('Enter correct customer')
    .nullable(),
});

const initialValues = {
  status: 'Available' as LoadStatus,
  /*pick: null,
  pickDate: undefined,
  deliver: null,
  deliverDate: undefined,*/
  weight: undefined,
  truckType: [],
  rate: undefined,
  totalCharges: undefined,
  currency: 'USD',
  bookedByUser: null,
  bookedByCompany: undefined,
  assignTo: [],
  checkInAs: '4ULogistics',
  truck: null,
  bookedWith: null,
};

export default function CreateLoadForm() {
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(LoadCreate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Created',
      });
      queryClient.invalidateQueries(['loads']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateLoad>({
    initialValues,
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values as unknown as CreateLoad);
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box sx={{ height: '100%' }}>
      <form
        onSubmit={formik.handleSubmit}
        onReset={formik.handleReset}
        style={{ height: '100%' }}
      >
        <Box sx={{ p: 3, height: '100%' }}>
          <Divider />
          <Box
            key="controls"
            sx={{
              maxHeight: 'calc(100% - 70px)',
              overflowX: 'hidden',
              overflowY: 'scroll',
            }}
          >
            <Box
              key="centerControls"
              sx={{ display: 'inline-flex', width: '100%' }}
            >
              <Box key="left" sx={{ p: 1, width: '50%' }}>
                <SelectField
                  fieldName="status"
                  label="Status"
                  items={LoadStatuses}
                  /*disabledItems={LoadStatuses.filter(
                  (status) =>
                    status !== 'Available' && status !== 'Not Available',
                )}*/
                  formik={formik}
                />
                {/*<SearchLocationField
                fieldName="pick"
                label="Pick Location"
                formik={formik}
              />
              <DateTimeField
                fieldName="pickDate"
                label="Pick Date"
                disablePast
                formik={formik}
              />*/}
                <TextField fieldName="weight" label="Weight" formik={formik} />
                <SelectMultiField
                  fieldName="truckType"
                  label="Truck Type"
                  items={TruckTypes}
                  formik={formik}
                />
                <SearchField<User>
                  fieldName="bookedByUser"
                  label="Booked By"
                  getSelectedLabel={(selectedItem) =>
                    `${selectedItem.fullName}`
                  }
                  getItemLabel={(item) => `${item.fullName}`}
                  getItemSecondLabel={(item) => `${item.jobTitle}`}
                  ListIcon={PersonIcon}
                  queryFn={UsersQuery}
                  orderby="fullName"
                  order="asc"
                  formik={formik}
                />
              </Box>
              <Box key="right" sx={{ p: 1, width: '50%' }}>
                {/*<SearchLocationField
                fieldName="deliver"
                label="Deliver Location"
                formik={formik}
              />
              <DateTimeField
                fieldName="deliverDate"
                label="Deliver Date"
                disablePast
                formik={formik}
              />*/}
                <TextField
                  fieldName="rate"
                  label="Rate"
                  number
                  formik={formik}
                />
                <TextField
                  fieldName="totalCharges"
                  label="Total Charges"
                  number
                  formik={formik}
                />
                <TextField
                  fieldName="currency"
                  label="Currency"
                  formik={formik}
                />
                <TextField
                  fieldName="checkInAs"
                  label="Check In As"
                  formik={formik}
                />
                <TextField
                  fieldName="bookedByCompany"
                  label="Booked By Company"
                  formik={formik}
                />
              </Box>
            </Box>
            <Box
              key="bottomControls"
              sx={{ marginTop: -2, marginBottom: 3, p: 1 }}
            >
              <TextField
                fieldName="ref"
                label="REF"
                multiline
                maxRows={3}
                formik={formik}
              />
              <SearchField<Truck>
                fieldName="truck"
                label="Truck"
                getSelectedLabel={(selectedItem) =>
                  `${selectedItem.truckNumber}`
                }
                getItemLabel={(item) => `${item.truckNumber}`}
                getItemSecondLabel={(item) =>
                  `${item.status}, ${item.licencePlate}`
                }
                ListIcon={LocalShippingIcon}
                queryFn={TrucksQuery}
                orderby="truckNumber"
                order="asc"
                formik={formik}
              />
              <SearchField<Customer>
                fieldName="bookedWith"
                label="Booked With"
                getSelectedLabel={(selectedItem) => `${selectedItem.name}`}
                getItemLabel={(item) => `${item.name}`}
                getItemSecondLabel={(item) => `${item.type}`}
                ListIcon={FactoryIcon}
                queryFn={CustomersQuery}
                orderby="name"
                order="asc"
                formik={formik}
              />
              <SearchMultiField<User>
                fieldName="assignTo"
                label="Assign To"
                getSelectedLabel={(selectedItem) => `${selectedItem.fullName}`}
                getItemLabel={(item) => `${item.fullName}`}
                getItemSecondLabel={(item) => `${item.jobTitle}`}
                ListIcon={PersonIcon}
                queryFn={UsersQuery}
                orderby="fullName"
                order="asc"
                formik={formik}
              />
            </Box>
          </Box>
          <Divider />
          <Box key="buttons" sx={{ paddingTop: 3, display: 'flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
              >
                Create
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading} />
    </Box>
  );
}
